@charset "utf-8";
* {                  /* 清除不同浏览器的不同外边距 */
	margin: 0;
	padding: 0;
}
/* 除去浮动*/
.clear {
	clear: both;
}
/* 水平居中 */
.center {
	margin-left: auto;
	margin-right: auto;
}
.tableDefault {
	border: 1px solid #F5EDFB;
	padding:0px;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 5px;
	border-collapse:collapse;
}
td{
	margin:0;
	padding:2px;	
}
.trDefault {
	border: 1px solid #F5EDFB;
}
/*取消超链接的下划线*/
a:link {
	text-decoration: none;
}
a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
a:active {
	text-decoration: none;
}
.model {
	height: 100%;
	width: 100%;


	top: 0px;
	left: 0px;
	display: none;
}
.modelContent {	
	width:400px;
	margin-right: auto;
	margin-left: auto;
	margin-top: 40px;
}
.div-out {
	margin-right: auto;
	margin-left: auto;
	margin-top: 0px;
	margin-bottom: 0px;
	width: 800px;
	height: auto;
}
.news {
	width: 800px;
	margin-right: auto;
	margin-left: auto;
	margin-top: 5px;
	margin-bottom: 5px;
}
.newsleft {
	float: left;
	height: 200px;
	width: 395px;
	margin-right: 10px;
	margin-bottom: 10px;
}
.newsRight {
	float: left;
	height: 200px;
	width: 395px;
	margin-right: 0px;
	margin-bottom: 10px;
	margin-left: 0px;
}
.mainPageUl {
	background-position: 0px 6px;
	padding-left: 8px;
	background-repeat: no-repeat;
}
.invisibleTable {
	border: none;
	width:100%;
}
.newsColumn{

	background:lightblue;
    line-height: 34px;
    height: 34px;
}
.newsShowByType_frame {
	width: 800px;
	position: relative;
	left:0;
	right:0;
}
.newsShowByType_left   {
	width: 138px;
	height: 610px;
	border: 1px solid #333333;
	margin-right: 2px;
	margin-top: 2px;
	position: absolute;
	padding-left: 40px;
	line-height: 30px;
	padding-top: 30px;		
}
.newsShowByType_rightTop   {
	width: 524px;
	height: 520px;
	margin-top: 2px;
	border-right: 1px solid #333333;
	border-top: 1px solid #333333;
	border-bottom: 0px;
	border-left: 1px solid #333333;
	position: absolute;
	left: 205px;
	padding-left: 40px;
	padding-right: 30px;
	line-height: 30px;
	padding-top: 30px;
}
.newsShowByType_rightBottom   {
	width: 524px;
	height: 260px;
	border-right: 1px solid #333333;
	border-top: 0px;
	border-bottom:1px solid #333333;
	border-left: 1px solid #333333;
	position: absolute;
	left: 205px;
	top: 353px;
	padding-left: 40px;
	padding-right: 30px;
	line-height: 30px;
	padding-top: 30px;
}
.newsShowByType_news_ul {
	list-style-image: url(/news/images/item.png);
